import React from "react";
import { Link } from "gatsby";
import Slider from "react-slick";
import arrowicon from "../../assets/images/home/arrow.svg";
import tileicon from "../../assets/images/home/tile-icon.svg";
import mapicon from "../../assets/images/home/mapicon.svg";
import slideicon from "../../assets/images/home/slideicon.svg";
import projecticon from "../../assets/images/home/projecticon.svg";
import herobanner from "../../assets/images/home/herobanner.png";
import aboutcover from "../../assets/images/home/aboutimage.png";
import product1 from "../../assets/images/home/product1.png";
import product2 from "../../assets/images/home/product2.png";
import product3 from "../../assets/images/home/product3.png";
import product4 from "../../assets/images/home/product4.png";
import tiles1 from "../../assets/images/home/tiles1.png";
import tiles2 from "../../assets/images/home/tiles2.png";
import tiles3 from "../../assets/images/home/tiles3.png";
import tiles4 from "../../assets/images/home/tiles4.png";
import project1 from "../../assets/images/home/project1.png";
import project2 from "../../assets/images/home/project2.png";
import project3 from "../../assets/images/home/project3.png";
import project4 from "../../assets/images/home/project4.png";
import fb from "../../assets/images/home/fb.svg";
import twitter from "../../assets/images/home/twitter.svg";
import linkedin from "../../assets/images/home/linkedin.svg";
